.Auth {
  margin: 20px auto;
  width: 80%;
  text-align: center;
  background-color: white;
  box-shadow: 0 2px 3px #ccc;
  border: 1px solid #eee;
  padding: 30px 50px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.Auth h1 {
  color: #444;
  text-transform: uppercase;
}

.Auth form {
  display: flex;
  flex-direction: column;
}

.Auth button {
  text-transform: uppercase;
  margin-top: 20px;
  margin-bottom: 40px;
}
.Auth form button {
  margin-bottom: 0;
}

@media (min-width:600px) {
  .Auth {
    width: 500px;
  }
}